.rendering-container {
  display: flex;
  gap: 8px;
}

.rendering-emoji-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 16px;
}

.rendering-emoji {
  font-size: 48px;
}

.rendering-header {
  text-align: center;
}

.rendering-vdom-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.rendering-real-dom-image {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  box-shadow: 2px 5px 10px 0px #0004;
}

.rendering-bottom-label {
  font-size: 12px;
  color: #555;
  padding-top: 16px;
  text-align: center;
}
